.about{
    


    .rect_design{
        position: absolute;
        left: -900px;
        top: 800px;

        

    }

    .circle{
        position: absolute;
        top: 650px;
        left: 300px;
        opacity: 60%;
    }

    .rectangle{
        position: absolute;
        top: 750px;
        left: 120px;
        opacity: 60%;
    }


    .grid{
        display: grid;
        grid-template-columns:repeat(2,1fr) ;
        margin-top: 150px;

        .shape{
         


            .photo{
               
             
               height: 500px;
               width: 400px;
               border-radius: 10px;
               background:#EBE8E8;
               margin-left: 200px;
               z-index: 1;
            }
          

        }

        .right_section{

            margin-top: 10px;

            .about_me{
               font-size: 60px;
               font-weight: 700;
               font-family: var(--secondary-font);
               padding-bottom: 10px;
              
             
            }

            .about_section{

               font-size: 20px;
               font-family: var(--primary--font);
               font-weight: 400;
               line-height: 28px;
               
            }

            .btn_about_me{
                font-size: 18px;
                    color: #fff;
                    background:var(--primary-color);
                    border-radius: 30px;
                    text-decoration: none;
                    padding: 10px 45px;
                    display: inline-block;
                    margin-top: 19px;
                    transition: transform 0.3s ease;

                  &:hover{
                   transform: scale(1.01) ;
                  }

                 
            }
        }


    }
}